@font-face {
    font-family: 'Friendly Fire';
    src: local('Friendly Fire'), url(../public/fonts/Friendly_Fire.ttf);
}
  
@font-face {
    font-family: 'Kenyan Coffee';
    src: local('Kenyan Coffee'), url(../public/fonts/kenyan_coffee_rg.otf);
}

html, body {
    background: rgb(42, 21, 82);
    color: white;
    margin: 1rem;
}

.App {    
  font-family: 'Friendly Fire';
  font-size: 2em;
  text-transform: uppercase;
  text-shadow:
    /* Basic Outline */
    -0.1em -0.1em 0 #000,
     0.1em -0.1em 0 #000,
    -0.1em  0.1em 0 #000,
     0.1em  0.1em 0 #000,
     0.0em -0.1em 0 #000,
     0.1em  0.0em 0 #000,
     0.0em  0.1em 0 #000,
    -0.1em  0.0em 0 #000;
  letter-spacing: 0.1em;

  margin: auto;
  max-width: 90rem;
}

.filterButton > div {
    transition: transform 0.1s;
    transform: scale(1.0);
}

.filterButton:hover > div,
.filterButton:active > div,
.filterButton.checked > div {
    transform: scale(0.9);
}

.react-select {
    width: 4em;
    background-color: rgb(242, 175, 28);
    border: solid rgb(200, 144, 21) 0;
    border-bottom: solid rgb(200, 144, 21) 0.2em;
    outline: solid black 0.1em;
    border-radius: 0.5rem;
    padding: 0 0.5rem;
    padding-bottom: 0.5rem;
    user-select: none;

    transition: border-color 0.1s, background-color 0.1s, color 0.1s, outline 0.1s;
}

.react-select *[class$="placeholder"] {
    transition: transform 0.1s;
    transform: scale(1.0);
}

.react-select:hover *[class$="placeholder"] {
    transform: scale(0.9);
}

.react-select:hover {
    border-color: transparent;
    outline: solid white 0.1em;
}

/* I hate css in js :cry: */
.react-select *[class$="menu"] {
    background-color: rgb(242, 175, 28);
    border: solid rgb(200, 144, 21) 0;
    border-bottom: solid rgb(200, 144, 21) 0.2em;
    outline: solid black 0.1em;
    border-radius: 0.5rem;
}

.react-select *[class$="option"] {
    transition: transform 0.1s, color 0.1s;
    transform: scale(1.0);
    color: white;
}

.react-select *[class$="option"]:hover {
    transform: scale(0.9);
    color: rgb(255, 71, 178);
}

.react-select *[class$="placeholder"],
.react-select *[class$="singleValue"],
.react-select *[class$="option"] {
    padding: 0 1rem;
}

.myDeck {
    position: relative;
    min-height: 30rem;
}

.myDeck.hello {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    font-family: Kenyan Coffee;
    font-size: 1.5rem;
    text-align: center;
}

.myDeck.hello > p {
    max-width: 40rem;
}

.myDeck > .row {
    display: flex;
    flex-wrap: wrap;
}

.myDeck > .characterArt {
    position: absolute;
    inset: 0;
    z-index: -1;
    background-repeat: no-repeat;
}

.topMenu {
    position: sticky;
    top: 1rem;
    z-index: 10;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    padding: 0.5rem 1rem ;
    margin-top: 1rem;
    margin-bottom: 2.5rem;
    border: solid rgb(68, 51, 154) 0;
    border-bottom: solid rgb(68, 51, 154) 0.1em;
    outline: solid black 0.1em;
    border-radius: 0.5rem;
    background-color: rgb(84, 64, 187);
}

.title {
    display: flex;
    align-items: center;
}

.title > .logo {
    width: 1.5em;
    aspect-ratio: 1/1;
}

.title > .text {
    margin-left: 1rem;
}

@media (max-width: 750px) {
    .title > .text {
        display: none;
    }
}
  

.topMenu > .costMenu {
    display: flex;
    gap: 1rem;
}

.filters {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    padding: 0.5rem 1rem ;
    margin-top: 1rem;
    margin-bottom: 2.5rem;
    border: solid rgb(68, 51, 154) 0;
    border-bottom: solid rgb(68, 51, 154) 0.1em;
    outline: solid black 0.1em;
    border-radius: 0.5rem;
    background-color: rgb(84, 64, 187);
}

.filters > .cardTypes {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
}

.filterButton {
    background-color: rgb(242, 175, 28);
    border: solid rgb(200, 144, 21) 0;
    border-bottom: solid rgb(200, 144, 21) 0.2em;
    outline: solid black 0.1em;
    border-radius: 0.5rem;
    padding: 0.1rem 0.5rem;
    padding-bottom: 0.5rem;
    user-select: none;

    transition: border-color 0.1s, background-color 0.1s, color 0.1s, outline 0.1s;
}

.filterButton:hover {
    border-color: transparent;
    outline: solid white 0.1em;
}

.filterButton:active {
    border-color: transparent;
    background-color: white;
}

.filterButton.checked {
    border-color: transparent;
    background-color: white;
    color: rgb(255, 71, 178);
}

.filterButton > div {
    transition: transform 0.1s;
    transform: scale(1.0);
}

.filterButton:hover > div,
.filterButton:active > div,
.filterButton.checked > div {
    transform: scale(0.9);
}

.content {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    align-items: flex-start;
    margin: 0 1rem;
}

footer {
    opacity: 0.7;
    margin-top: 20em;
    margin-bottom: 3em;
    color: white !important;
    font-family: Kenyan Coffee;
    font-size: 0.9rem;
    text-align: right;
    text-shadow:
        /* Basic Outline */
        -1px -1px  0 #000,
         1px -1px  0 #000,
        -1px  1px  0 #000,
         1px  1px  0 #000,
         0   -1px  0 #000,
         1px  0    0 #000,
         0    1px  0 #000,
        -1px  0    0 #000;
  letter-spacing: 0.15em;
}

footer a {
    color: inherit !important;
}